:root {
    /* Light */
    --background-color-light: #f1f1f1;
    --text-color-light: #333333;
    --color-light: rgb(175,0,108);
    /* Dark */
    --background-color-dark:#333333;
    --text-color-dark: #ffffff;
    --color-dark: rgb(255,215,0);
    /* Application */
    --primary-color: #000000;
    --background-color: #000000;
    --text-color: #000000;
}